@mixin base() {
    @include set-text-style($text-style-heading-4);

    @include breakpoint(medium) {
        display: none;
    }

    color: $color-link-inverse;
    position: absolute;
    right: 35px;
    top: calc((150/2.25) * 1px); //150 is height of logo should be a variable - not sure why 2.25 works

    &:hover {
        color: $color-link-inverse;
        text-decoration: none;
    }
}

&__toggle-link {
    @include base;
}