@mixin base() {
    @include set-text-style($text-style-heading-4);

    color: $color-link-inverse;
    display: block;
    padding: 10px 14px;

    &:hover {
        background-color: $color-link-inverse-hover-background;
        color: $color-link-inverse-hover;
        text-decoration: none;
    }

    &:active {
        background-color: $color-link-inverse-active-background;
        color: $color-link-inverse-hover;
    }
}

&__link {
    @include base;

    &--active {
        @include base;
        background-color: $color-link-inverse-active-background;
        color: $color-link-inverse-hover;
    }
}