@mixin base() {
    @include breakpoint(medium) {
        display: block;
        border-top: none;
    }

    border-top: 2px solid $color-border-primary;
    display: none;
    font-size: 0; // remove whitespace between list items
    list-style: none;
    margin: 0;
    padding: 0;
}

&__list {
    @include base;

    &[data-primary-nav-open="true"] {
        display: block;
    }
}