@mixin base() {
    @include set-text-style($text-style-heading-4);

    background-color: $color-link-inverse-background;
    color: $color-link-inverse;
    left: -2000px;
    padding: 15px 20px;
    position: absolute;
    top: 0;

    &:hover {
        color: $color-link-inverse-hover;
    }

    &:active {
        color: $color-link-inverse-active;
    }

    &:focus {
        left: 0;
    }
}

&__link {
    @include base;
}