@mixin base() {
    padding: 20px 100px 20px 60px;
    position: relative;

    &::before {
        background-image: map-get($component-panel-info-icon, icon);
        background-repeat: no-repeat;
        content: "";
        display: block;
        height: map-get($component-panel-info-icon, height);
        left: 20px;
        position: absolute;
        top: 25px;
        width: map-get($component-panel-info-icon, width);
    }
}

&-global-alert {
    @include base;
    @include panel($panel-info);

    &--positive {
        @include base;
        @include panel($panel-positive);

        &::before {
            background-image: map-get($component-panel-positive-icon, icon);
            height: map-get($component-panel-positive-icon, height);
            width: map-get($component-panel-positive-icon, width);
        }
    }

    &--warning {
        @include base;
        @include panel($panel-warning);

        &::before {
            background-image: map-get($component-panel-warning-icon, icon);
            height: map-get($component-panel-warning-icon, height);
            width: map-get($component-panel-warning-icon, width);
        }
    }

    &--negative {
        @include base;
        @include panel($panel-negative);

        &::before {
            background-image: map-get($component-panel-negative-icon, icon);
            height: map-get($component-panel-negative-icon, height);
            width: map-get($component-panel-negative-icon, width);
        }
    }

    @import "heading";
    @import "content";
    @import "close";
}